
#ContentBody {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  width: 100%;
  height: 100%;
  background-image: url('bg.jpg');
  position: absolute;

background-repeat: no-repeat;
-webkit-background-size: cover;
-moz-background-size: cover;
-o-background-size: cover;
background-size: cover;
padding: 0;
z-index: 1;
}
#login-container{
  width: 95%;
  height: 95vh;

padding: 50px 0 350px 0;

background-color: rgba(0, 0, 0, 0.8);
color:whitesmoke;
border: 5px solid rgb(21 , 21, 21);
border-radius: 5px;
font-size: 12px;
z-index: 1;
animation: 0.4s ease-out 0s 1 slideInFromLeft;

text-align: center;
box-shadow: 0 0 10px rgb(123, 176, 59),  0 0 40px rgb(123, 176, 59),  0 0 10px rgb(123, 176, 59);



}


.alertlogin {

padding: 5px;
    font-size: 16px;
    font-weight: bold;
}


.alert {

  padding: 5px;
      font-size: 14px;
      font-weight: bold;
  }


#login-container input{
  width: 320px;


}


#login-container #usr {
  width: 200px;


}


.loginbtn {
  background-color: rgb(21, 21, 21);
  color: #ffffff;
  border: 2px dashed rgb(123, 176, 59);
  padding: 10px 20px;
  font-size: 13px;
  cursor: pointer;
  border-radius: 5px;
  margin-left: 10px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
transition: 0.2s;
width: 100px;
}
.loginbtn:hover, .loginbtn-big:hover {
background-color: black;
border: 2px dashed rgb(123, 176, 59);
box-shadow: 0 0 10px rgb(123, 176, 59),  0 0 40px rgb(123, 176, 59),  0 0 10px rgb(123, 176, 59);

}

.cstminpt {

background-color: rgb(215, 235, 206);

padding: 5px;
font-size: 15px;
border-radius: 10px;
text-align: left;
margin-top: 10px;
transition: 0.2s;

}

.cstminpt:focus{  box-shadow: 0 0 10px rgb(123, 176, 59),  0 0 40px rgb(123, 176, 59),  0 0 10px rgb(123, 176, 59);
}
#login-logo{

  animation: login 5s infinite;

}
.text_1 {
  animation: text1;
}

.text_2 {
  animation: text2;
}
.text_3 {
  animation: text3;
}
.text_1, .text_2, .text_3 {
  color:rgb(123, 176, 59);
  font-size: 19px;
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  position: relative;
  animation-duration: 15s;
  animation-timing-function: steps(70, end);
  animation-iteration-count: infinite;
}

.text_1::after, .text_2::after, .text_3::after {
  content: "|";
  color: white;
  position: absolute;
  right: 0;
  animation-duration: 1s;
  animation-timing-function: steps(1, end);
}
  /* head article animated 3 words animations */

@keyframes text3 {
  0%, 30%, 60%, 100% {
    width: 0;
  }
  
  70%, 90% {
    width: 17em;
  }
}

@keyframes text2 {
  0%, 30%, 60%, 100% {
    width: 0;
  }
  
  40%, 50% {
    width: 17em;
  }
}

@keyframes text1 {
  0%, 30%, 60%, 100% {
    width: 0;
  }
  10%, 20% {
    width: 20em;
  }
}



@keyframes login {
  0% {   transform: scaleX(1);  }
  85% {   transform: scaleX(1);  }

  95% {  transform: scaleX(-1);  }
  100% {  transform: scaleX(1);  }

}

@media screen and (max-width: 700px) {
  #login-container{
padding: 100px 0 150px 0;

}



@keyframes text3 {
  0%, 30%, 60%, 100% {
    width: 0;
  }
  
  70%, 90% {
    width: 19em;
  }
}

@keyframes text2 {
  0%, 30%, 60%, 100% {
    width: 0;
  }
  
  40%, 50% {
    width: 18em;
  }
}

@keyframes text1 {
  0%, 30%, 60%, 100% {
    width: 0;
  }
  10%, 20% {
    width: 22em;
  }
}







.text_1, .text_2, .text_3 {

  font-size: 15px;
}
#login-logo{

  width: 60% !important;
}

}

.cstmlabl::after {
    content: '@domain.com';
    position: relative;
    top: -25px;
    left: 94px;
    font-family: arial, helvetica, sans-serif;
    font-size: 12px;
    display: block;
    color: black;
    font-weight: bold;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

